
import { Avatar, Box, Button, Checkbox, Container, createTheme, CssBaseline, FormControlLabel, Grid, Link, TextField, Typography } from "@mui/material";
import { useState } from "react";
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { useFormik } from 'formik';
import * as Yup from 'yup'
import { useDispatch } from "react-redux";
import { setToken } from "../../Reducers/Slices/LoginSlice";
import { useNavigate } from "react-router-dom";
import { LoginAuthenticacion } from "../../Services/Comisiones/Authenticacion";
import { loginDto } from "../../Services/Comisiones/Interfaces/Login";
import LoadingButton from '@mui/lab/LoadingButton';
import { getDataDevice } from '../../Utils/GetDataNavigation'
import { CSnackBars } from "../../Components/snackBars";
interface MessageError {
    message: string;
    show: boolean;
}
const initialMessageError: MessageError = { message: "", show: false };

const Login = () => {

    const dispatch = useDispatch();
    const [loading, setLoading] = useState(false);
    const [{ message, show }, setMessage] = useState(initialMessageError);
    const updateToken = (token: string) => {
        dispatch(
            setToken({
                token: token
            })
        )
    }
    const iniciarSession = async (value: loginDto) => {
        try {
            setLoading(true);
            setMessage(initialMessageError);
            const { data } = await LoginAuthenticacion(value);
            if (data.status == 1) {
                updateToken(data.data.token);
                setLoading(false);
                navigate('/home')
            }
            else {
                setErrors({
                    usuario: 'Credenciales no validas'
                });
                setMessage({ message: data.message, show: true });
                setLoading(false);
            }
        } catch (error) {
            setLoading(false);
            setMessage({ message: "sistema se encuentra en mantemiento", show: true });
        }
    }
    const navigate = useNavigate();

    const { values, handleSubmit, handleBlur, handleChange, handleReset, errors, getFieldProps, setErrors } = useFormik({
        initialValues: {
            usuario: '',
            password: '',
            browserName: getDataDevice().browserName,
            browserVersion: getDataDevice().browserVersion,
            osName: getDataDevice().osName
        },
        onSubmit: async (value: loginDto) => {
            iniciarSession(value)
        },
        validationSchema: Yup.object({
            usuario: Yup.string()
                .min(5, 'El valor debe ser almenos 3 caracteres')
                .required('Es requerido'),
            password: Yup.string()
                .min(5, 'El valor debe ser almenos 3 caracteres')
                .required('Es requerido'),
        })
    });

    return (
        <>
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                        <LockOutlinedIcon />
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Comisiones
                    </Typography>
                    <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="email"
                            label="Usuario"
                            name="usuario"
                            autoComplete="off"
                            autoFocus
                            value={values.usuario}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            helperText={errors.usuario}
                        />
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            name="password"
                            label="Password"
                            type="password"
                            id="password"
                            value={values.password}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            autoComplete="current-password"
                            helperText={errors.password}
                        />
                        {/*  <FormControlLabel
                                control={<Checkbox value="remember" color="primary" />}
                                label="Remember me"
                            /> */}
                        <LoadingButton
                            type="submit"
                            loading={loading}
                            fullWidth
                            variant="contained"
                        >
                            <span>INGRESAR</span>
                        </LoadingButton>
                        <Grid container>
                            {/*  <Grid item xs>
                                    <Link href="#" variant="body2">
                                        Forgot password?
                                    </Link>
                                </Grid>
                                <Grid item>
                                    <Link href="#" variant="body2">
                                        {"Don't have an account? Sign Up"}
                                    </Link>
                                </Grid> */}
                        </Grid>
                    </Box>
                </Box>
                <CSnackBars message={message} open={show} />
            </Container>
        </>
    );
}

export default Login;